<!--
 * @Autor: linxu
 * @Date: 2022-03-03 11:38:28
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-02 11:28:41
 * @Description: 项目列表
-->
<template>
  <div v-loading="loading" element-loading-text="加载中">
    <BMainTopComponent />
    <div class="project-list">
      <!-- 筛选 -->
      <div class="search-box">
        <div class="search-item">
          <span>项目名称:</span>
          <el-input placeholder="请输入项目名称" v-model="searchForm.ProjectName"> </el-input>
        </div>
        <div class="search-item">
          <span>项目类型:</span>
          <el-select
            v-model="searchForm.ProjectType"
            :popper-append-to-body="false"
            placeholder="请选择项目类型"
          >
            <el-option label="全部" value=""></el-option>
            <el-option
              v-for="item in ProjectTypeEnum"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
        <div class="search-item">
          <span>项目地址:</span>
          <!-- 省市县级联选择器  :defaultPosition="searchForm.position"-->
          <position-cascader
            :defaultPosition="searchForm.position"
            @positionChange="positionChange"
          />
        </div>
        <div class="search-item">
          <span>项目状态:</span>
          <el-select
            v-model="searchForm.Status"
            :popper-append-to-body="false"
            placeholder="请选择项目状态"
          >
            <el-option label="全部" value=""></el-option>
            <el-option
              v-for="item in ProjectStatusEnum"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
        <div class="search-item date-type">
          <span>项目生效时间:</span>
          <el-date-picker
            class="input-with-select"
            v-model="searchForm['effectTime']"
            type="daterange"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            range-separator="-"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            placement="bottom-start"
          >
          </el-date-picker>
        </div>
        <div class="search-item date-type">
          <span>项目创建时间:</span>
          <el-date-picker
            class="input-with-select"
            v-model="searchForm['creatTime']"
            type="daterange"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            range-separator="-"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            placement="bottom-start"
          >
          </el-date-picker>
          <!-- <el-date-picker
            class="input-with-select"
            v-model="searchForm['creatTime']"
            type="datetimerange"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-DD HH:mm:ss"
            :default-time="['00:00:00', '23:59:59']"
            range-separator="-"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            placement="bottom-start"
          >
          </el-date-picker> -->
        </div>
        <div class="search-btn-box">
          <el-button type="text" class="reset-btn" @click="reset()">清空筛选</el-button>
          <el-button type="primary" class="query-btn" @click="resetCurrentParams()"
            >查 询</el-button
          >
        </div>
      </div>
      <div class="operation-box">
        <el-button type="primary" class="add-btn" @click="handleAddProject()">新建项目</el-button>
        <el-checkbox v-model="checked"> 即将到期项目</el-checkbox>
      </div>

      <!-- 表格 -->
      <!-- <div class="table-box"> -->
      <TableList
        :options="options"
        :columns="columns"
        :dataSource="dataSource"
        :dataTotal="dataTotal"
        :pagination="paginationCfg"
        @handleChangePage="handleChangePage"
        @handleChangeSize="handleChangeSize"
        border
      >
        <el-table-column
          slot="operation"
          label="操作"
          align="center"
          v-bind="options && options.props"
        >
          <template slot-scope="scope">
            <div class="operate-group" style="text-align: right">
              <el-button v-if="scope.row.Status === 1" type="text" @click="handleOpen(scope.row)">
                开启项目
              </el-button>
              <el-button
                v-if="scope.row.Status === 2"
                type="text"
                style="color: #d9001b"
                @click="handleClose(scope.row)"
              >
                完结项目
              </el-button>
              <el-button type="text" @click="handleShowDetail(scope.row)"> 项目详情 </el-button>
              <el-button type="text" @click="handleManageCrew(scope.row)"> 工程管理 </el-button>
              <el-button
                type="text"
                v-if="scope.row.Status !== 1"
                @click="handleManageCost(scope.row)"
              >
                完工记录
              </el-button>
            </div>
          </template>
        </el-table-column>
      </TableList>
      <!-- </div> -->
    </div>
    <!-- 新增项目弹窗 -->
    <AddOrEditProjectDialog
      v-if="dialogAddVisible"
      @closeDialog="(val) => closeDialog('dialogAddVisible', val)"
    />

    <!-- 开启项目弹窗 -->
    <OpenProjectDialog
      v-if="dialogOpenVisible"
      :projectInfo="selProject"
      @handleOpenConfirm="handleOpenConfirm"
      @closeDialog="(val) => closeDialog('dialogOpenVisible', val)"
    />
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'
import PositionCascader from '@/components/PositionCascader'
import AddOrEditProjectDialog from './components/AddOrEditProjectDialog'
import OpenProjectDialog from './components/OpenProjectDialog'
import _data from './components/data.js'

export default {
  name: 'ProjectList',
  mixins: [paginationConfigMixin],
  components: {
    PositionCascader,
    AddOrEditProjectDialog,
    OpenProjectDialog
  },
  data() {
    return {
      ProjectTypeEnum: _data.ProjectTypeEnum, // 项目类型枚举
      ProjectStatusEnum: _data.ProjectStatusEnum, // 项目状态枚举
      // 筛选条件
      searchForm: {
        ProjectName: '',
        ProjectType: '',
        position: '',
        Status: '',
        effectTime: '',
        creatTime: ''
      },
      checked: false,
      // 表格
      columns: _data.listColumns(this),
      dataSource: [],
      options: {
        // height: '100%',
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 266 // 操作列宽度
        }
      },
      loading: false,

      dialogAddVisible: false, // 项目新建弹窗
      dialogOpenVisible: false, // 开启项目弹窗
      selProject: null // 项目数据
    }
  },
  watch: {
    // 即将到期项目 查询切换
    checked(newVal, oldVal) {
      this.queryTableData()
    }
  },
  mounted() {
    this.queryTableData()
  },
  methods: {
    ...mapActions(['toggleCurrentProject']),
    queryTableData() {
      this.loading = true
      const query = {
        Page: this.paginationCfg.currentPage,
        PageSize: this.paginationCfg.pageSize
      }
      const params = {
        ...this.searchForm,

        StartTime: this.searchForm.effectTime && this.searchForm.effectTime[0],
        EndTime: this.searchForm.effectTime && this.searchForm.effectTime[1],
        StartCreateTime: this.searchForm.creatTime && this.searchForm.creatTime[0],
        EndCreateTime: this.searchForm.creatTime && this.searchForm.creatTime[1],
        IsDue: this.checked

        // ProjectArea: this.searchForm.position
      }
      // console.log('params------', params)

      this.$api.project
        .GetGensetProjectManage(params, query)
        .then((res) => {
          // console.log('project-list', res)
          if (res.Code === 1) {
            this.$set(this, 'dataSource', res.Data.Data)
            this.dataTotal = res.Data.TotalItemCount
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },

    // 重置筛选条件
    reset() {
      this.$set(this, 'searchForm', {
        ProjectName: '',
        ProjectType: '',
        position: '',
        Status: '',
        effectTime: '',
        creatTime: ''
      })
    },

    // 筛选条件--级联选择器change事件
    positionChange(data) {
      // console.log(data)
      this.searchForm.position = data.crewPosition
    },

    // 新建项目
    handleAddProject() {
      this.dialogAddVisible = true
    },
    closeDialog(key, val) {
      this[key] = false
      if (val && val.refresh) {
        this.queryTableData()
      }
    },

    /* 表格操作列事件 */

    // 开启项目
    handleOpen(row) {
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '开启项目',
        message: h('div', [
          h('p', [
            h('span', '确定开启项目“'),
            h('em', { style: 'color: #1ABC9C;margin: 0 4px;' }, row.ProjectName),
            h('span', '”吗？')
          ]),
          h('p', '开启操作不可取消，请谨慎操作。')
        ]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.changeProjectStatus(row.ProjectId, 2)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
      // this.selProject = row
      // this.dialogOpenVisible = true
    },
    handleOpenConfirm(val) {
      console.log(val)
      this.changeProjectStatus(val.projectId, val.status)
    },
    handleOpenV1(row) {
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '开启项目',
        message: h('div', [
          h('p', [
            h('span', '确定开启项目'),
            h('em', { style: 'color: #1ABC9C;margin: 0 4px;' }, row.ProjectName),
            h('span', '吗？')
          ]),
          h('p', '开启操作不可取消，请谨慎操作。')
        ]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // this.$message({
          //   type: 'success',
          //   message: '开启项目成功!'
          // })
          this.changeProjectStatus(row.ProjectId, 2)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },

    // 完结项目
    handleClose(row) {
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '完结项目',
        message: h('div', [
          h('p', [
            h('span', '确定完结项目“'),
            h('em', { style: 'color: #1ABC9C,margin: 0 4px;' }, row.ProjectName),
            h('span', '”吗？')
          ]),
          h('p', '完结后，项目相关信息将不再支持编辑，操作不可取消，请谨慎操作。')
        ]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // this.$message({
          //   type: 'success',
          //   message: '完结项目成功!'
          // })
          this.changeProjectStatus(row.ProjectId, 3)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },

    changeProjectStatus(projectId, status) {
      const params = {
        id: projectId,
        status: status
      }
      this.$api.project.ChangeGensetProjectStatus(params).then((res) => {
        console.log('project-status', res)
        if (res.Code === 1) {
          this.queryTableData()
          this.$message({
            type: 'success',
            message: res.Message
          })
        }
      })
    },

    // 跳转项目详情
    handleShowDetail(row) {
      if (row) this.toggleCurrentProject(row)
      // this.$router.push({ path: '/project/project-detail' })
      window.open('/#/project/project-detail')
    },

    // 跳转工程管理
    handleManageCrew(row) {
      if (row) this.toggleCurrentProject(row)
      // this.$router.push({ path: '/project/crew-manage' })
      window.open('/#/project/crew-manage')
    },

    // 跳转费用管理
    handleManageCost(row) {
      if (row) this.toggleCurrentProject(row)
      // this.$router.push({ path: '/project/completion-record' })
      window.open('/#/project/completion-record')
    }
  }
}
</script>

<style lang="less" scoped>
.project-list {
  display: initial !important;
  .operation-box {
    margin-bottom: 6px;
    .add-btn {
      width: 98px;
      background-color: #1abc9c;
      color: #fff;
      margin-bottom: 10px;
      display: flex;
    }
  }
  .table-box {
    border: 1px solid #e4e4e4;
    padding-bottom: 16px;
    flex: 1;
  }
}
</style>



