<!--
 * @Autor: linxu
 * @Date: 2022-03-04 10:10:05
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-29 17:13:17
 * @Description: 项目新增/编辑 弹窗
-->
<template>
  <el-dialog
    :title="projectId ? '编辑项目' : '新增项目'"
    class="project-addedit-dialog"
    :visible.sync="dialigVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="close()"
  >
    <el-form :model="form" :rules="rules" ref="projectForm" :label-width="formLabelWidth">
      <el-row>
        <el-col :span="12">
          <el-form-item label="项目名称" prop="ProjectName">
            <el-input
              v-model="form.ProjectName"
              placeholder="请输入项目名称"
              autocomplete="off"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="项目编号" prop="ProjectNumber">
            <el-input
              v-model="form.ProjectNumber"
              placeholder="请输入项目编号"
              autocomplete="off"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>

      <el-form-item label="项目类型" prop="ProjectType">
        <el-select v-model="form.ProjectType" placeholder="请选择项目类型">
          <!-- <el-option label="请选择" value=""></el-option> -->
          <el-option
            v-for="item in ProjectTypeEnum"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="项目地址" prop="ProjectArea">
        <position-cascader :defaultPosition="form.ProjectArea" @positionChange="positionChange" />
      </el-form-item>
      <!-- 详细地址 -->
      <el-form-item label="" prop="ProjectAddress">
        <el-input
          v-model="form.ProjectAddress"
          placeholder="请输入详细地址"
          autocomplete="off"
        ></el-input>
      </el-form-item>

      <el-form-item label="项目起止时间" prop="contractTime">
        <el-date-picker
          class="input-with-select"
          v-model="form['contractTime']"
          type="daterange"
          format="yyyy-MM-dd"
          value-format="yyyy-MM-dd"
          range-separator="-"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          placement="bottom-start"
        >
        </el-date-picker>
      </el-form-item>
      <el-row>
        <el-col :span="12">
          <el-form-item label="功率需求" prop="TotalPower">
            <el-input
              v-model="form.TotalPower"
              placeholder="请输入项目功率需求"
              autocomplete="off"
            ></el-input>
            <span class="unit">Kw</span>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="租赁台数" prop="Quantity">
            <el-input
              v-model="form.Quantity"
              placeholder="请输入租赁台数"
              autocomplete="off"
            ></el-input>
            <span class="unit">台</span>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="项目租金" prop="TotalMoney">
            <el-input
              v-model="form.TotalMoney"
              placeholder="请输入项目租金"
              autocomplete="off"
            ></el-input>
            <span class="unit">元</span>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="resetForm('projectForm')">取 消</el-button>
      <el-button type="primary" @click="submitForm('projectForm')">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import PositionCascader from '@/components/PositionCascader'
import _data from './data.js'
const { ProjectTypeEnum } = _data
export default {
  name: 'AddOrEditProjectDialog',
  props: {
    projectId: {
      type: Number,
      default: null
    }
  },
  components: {
    PositionCascader
  },
  data() {
    return {
      ProjectTypeEnum,
      dialigVisible: true,
      formLabelWidth: '100px',
      form: {
        ProjectName: '',
        ProjectNumber: '',
        ProjectType: '',
        ProjectArea: '',
        ProjectAddress: '',
        contractTime: '',
        TotalPower: null,
        Quantity: null,
        TotalMoney: null
      },
      rules: {
        ProjectName: [
          { type: 'string', required: true, message: '请输入项目名称', trigger: 'blur' },
          { max: 15, message: '长度不超过 15 个字符', trigger: 'blur' }
        ],
        ProjectNumber: [
          { type: 'string', required: true, message: '请输入项目编号', trigger: 'blur' },
          { max: 20, message: '长度不超过 20 个字符', trigger: 'blur' }
        ],
        ProjectType: [{ required: true, message: '请选择项目类型', trigger: 'change' }],
        ProjectArea: [{ required: true, message: '请选择项目地址', trigger: 'change' }],
        contractTime: [{ required: true, message: '请选择项目起止时间', trigger: 'change' }],
        TotalPower: [
          { required: true, message: '请输入功率需求', trigger: 'blur' },
          {
            pattern: /^(0|[1-9][0-9]{0,5})([.][0-9]{1,2})?$/,
            message: '限制整数位不超过6位,小数点不超过2位',
            trigger: 'blur'
          }
        ],
        Quantity: [
          { required: true, message: '请输入租赁台数', trigger: 'blur' },
          { pattern: /^[1-9]\d?$/, message: '仅可输入不超过两位数的非零正整数', trigger: 'blur' }
        ],
        TotalMoney: [
          { required: true, message: '请输入项目租金', trigger: 'blur' },
          {
            pattern: /^(0|[1-9][0-9]{0,7})([.][0-9]{1,2})?$/,
            message: '限制整数位不超过8位,小数点不超过2位',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  mounted() {
    if (this.projectId) {
      // 编辑
      const query = { id: this.projectId }
      this.$api.project.GetGensetProject(query).then((res) => {
        if (res.Code === 1) {
          const StartDate = res.Data.StartDate,
            EndDate = res.Data.EndDate
          if (StartDate && EndDate) {
            res.Data.contractTime = [
              this.$moment(StartDate).format('YYYY-MM-DD'),
              this.$moment(EndDate).format('YYYY-MM-DD')
            ]
          }
          res.Data.ProjectType = parseInt(res.Data.ProjectType)
          this.$nextTick(() => {
            this.$set(this, 'form', res.Data)
          })
        }
      })
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const params = {
            ...this.form,
            StartDate: this.form.contractTime[0],
            EndDate: this.form.contractTime[1]
          }
          if (this.projectId) {
            const query = { id: this.projectId }
            this.editProject(params, query)
            return
          }
          this.addProject(params)
        } else {
          return false
        }
      })
    },
    // 新建
    addProject(params) {
      this.$api.project.AddGensetProject(params).then((res) => {
        if (res.Code === 1) {
          this.$message({
            type: 'success',
            message: '新建项目成功!'
          })
          this.$emit('closeDialog', { refresh: true })
        } else {
          this.$message({
            type: 'error',
            message: '新建项目失败，请重新提交!'
          })
        }
      })
    },
    // 编辑
    editProject(params, query) {
      console.log(params, query)
      this.$api.project.EditGensetProject(params, query).then((res) => {
        if (res.Code === 1) {
          this.$message({
            type: 'success',
            message: '编辑项目成功!'
          })
        } else {
          this.$message({
            type: 'error',
            message: '编辑项目失败，请重新提交!'
          })
        }
        this.$emit('closeDialog', { refresh: true })
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.close()
    },
    close() {
      this.$emit('closeDialog')
    },

    positionChange(data) {
      this.form.ProjectArea = data.crewPosition
      this.form.ProjectAddress = data.crewPosition
    }
  }
}
</script>

<style lang="less" scoped></style>



