<!--
 * @Autor: linxu
 * @Date: 2022-03-04 10:10:05
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-09 11:25:13
 * @Description: 开启项目弹窗
-->
<template>
  <el-dialog
    title="开启项目"
    class="open-project-dialog"
    width="70%"
    :visible.sync="dialigVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="close()"
  >
    <div class="dialog-content">
      <div class="project-baseinfo">
        <p>
          <span>项目名称：</span>
          <span>{{ project.ProjectName }}</span>
        </p>
        <p>
          <span>项目编号：</span>
          <span>{{ project.ProjectNumber }}</span>
        </p>
        <p>
          <span>项目状态：</span>
          <span>{{ ProjectStatusEnum.find((item) => item.value == project.Status).label }}</span>
        </p>
        <p>
          <span>项目创建时间：</span>
          <span>{{ $moment(project.CreateTime).format('yyyy-MM-DD HH:mm:ss') }}</span>
        </p>
      </div>
      <p class="clear-tip" v-if="gensetTableData && gensetTableData.length > 0">
        提示：请先选择需要重置累计数据的机组，提交确认开启项目后将同时执行数据重置
      </p>

      <div class="genset-list-container">
        <div
          class="genset-item-container"
          v-for="gensetItem in gensetTableData"
          :key="gensetItem.GeneratorSetId"
          :class="{ 'genset-checked': gensetItem.checked }"
        >
          <div class="genset-box genset-info">
            <el-checkbox class="checkbox" v-model="gensetItem.checked">确认重置</el-checkbox>
            <p>
              <span>机组名称：{{ gensetItem.DisplayName }}</span>
            </p>
            <p>
              <span>机组编号：{{ gensetItem.CollectorToken }}</span>
            </p>
          </div>
          <div class="genset-box genset-data">
            <p>期初底座油箱油量：300L</p>
            <p>期末底座油箱油量：30L</p>
            <p>加油次数：3</p>
            <p>机组燃油用量：470L</p>
            <p>机组平均油耗：33.33g/Kwh</p>
            <p>机组平均运行时间：480 h 35 min</p>
            <p>机组发电量：2345.55Kwh</p>
            <p>机组无故障时间：2000 h 35 min</p>
            <p>机组超功率运行时间：72 h 35 min</p>
          </div>
        </div>
      </div>
    </div>

    <div slot="footer" class="dialog-footer">
      <p class="tip" v-if="gensetTableData && gensetTableData.length > 0">
        <span>已选择{{ selectNum }}台，</span> <span>共{{ allNum }}台</span>
      </p>
      <el-button @click="close()">取 消</el-button>
      <el-button type="primary" @click="submitForm()">确认开启项目</el-button>
    </div>
  </el-dialog>
</template>

<script>
import _data from './data.js'
export default {
  name: 'OpenProjectDialog',
  props: {
    projectInfo: {
      type: Object,
      default: {}
    }
  },
  watch: {
    projectInfo: {
      handler: function (newVal, oldVal) {
        console.log('projectInfo', newVal, oldVal)
        this.project = newVal
      },
      deep: true
    }
  },
  computed: {
    allNum() {
      return this.gensetTableData.length
    },
    selectNum() {
      const newData = this.gensetTableData.filter((item) => item.checked == true)
      return newData.length
    }
  },
  data() {
    return {
      ProjectTypeEnum: _data.ProjectTypeEnum, // 项目类型枚举
      ProjectStatusEnum: _data.ProjectStatusEnum, // 项目状态枚举
      loading: false,
      dialigVisible: true,
      project: this.projectInfo,
      gensetTableData: []
    }
  },
  mounted() {
    this.queryGensetData()
  },
  methods: {
    queryGensetData() {
      const query = {
        Page: 1,
        PageSize: 99
      }
      const params = {
        ProjectId: this.project.ProjectId
      }
      this.$api.project.GetGensetProjectJoinGensetManageExpand(params, query).then((res) => {
        if (res.Code === 1) {
          this.gensetTableData = res.Data.Data.map((item) => {
            return {
              ID: item.ProjectJoinGensetId,
              IsParallel: item.IsParallel, // 是否并机
              IsFenceOpen: item.IsFenceOpen, // 是否开启电子围栏
              IsStrategy: item.IsStrategy, // 是否维保策略
              ...item.GeneratorSet,
              checked: false
            }
          })
          console.log(this.gensetTableData)
        }
      })
    },

    // 表单提交
    submitForm() {
      this.$emit('handleOpenConfirm', {
        projectId: this.project.ProjectId,
        status: 2
      })
    },
    close() {
      this.$emit('closeDialog')
    }
  }
}
</script>

<style lang="less" scoped>
@import '~@/styles/variables.less';
.dialog-content {
  .project-baseinfo {
    display: flex;
    justify-content: space-between;
  }
  .clear-tip {
    color: red;
    margin: 16px 0;
  }
  .genset-list-container {
    .genset-item-container {
      border: 1px solid #fff;
      border-radius: 4px;
      &:not(:last-child) {
        margin-bottom: 16px;
      }
      .genset-box {
        padding: 16px;
        position: relative;
        &.genset-info {
          border-bottom: 1px solid #fff;
          p {
            line-height: 24px;
          }
          .checkbox {
            position: absolute;
            right: 16px;
          }
        }

        &.genset-data {
          display: flex;
          flex-wrap: wrap;
          p {
            width: 33.33%;
            line-height: 24px;
          }
        }
      }

      &.genset-checked {
        border: 1px solid @theme;
        .genset-info {
          border-bottom: 1px solid @theme;
        }
      }
    }
  }
}
p.tip {
  color: red;
  margin-bottom: 8px;
}
</style>



